<template>
  <div class="alumniInformation" v-loading.body="bodyLoading">
    <div class="mainContainer">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="handleAdd">+ 新建</div>
        <div slot="right" @click="handleExport" icon="el-icon-plus" style="float: left; margin-right: 10px; color: #03B187; cursor: pointer;"> <SvgIcon icon-class="icondaoru" size="18px" style="float: left;margin: 32px 5px 0 0"></SvgIcon> 导入</div>
      </crumb>
      <div class="listQuery">
        <el-form ref="listQuery" :model="listQuery" label-width="90px">
          <el-row>
            <el-col :span="6">
              <el-form-item label="入学年级：">
                <el-select v-model="listQuery.grade" placeholder="请选择入学年级" style="width: 90%" clearable @keyup.enter.native="getList">
                  <el-option
                    v-for="item in options"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="所属学院：">
                <el-select v-model="listQuery.academyId" placeholder="请选择所属学院" style="width: 90%" @keyup.enter.native="getList">
                  <el-option
                    v-for="item in academyOption"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="所属专业：">
                <el-select v-model="listQuery.specialtyId" placeholder="请选择所属专业" style="width: 90%" @keyup.enter.native="getList">
                  <el-option
                    v-for="item in specialtyOption"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="所属班级：">
                <el-select v-model="listQuery.slaveClassCode" placeholder="请选择所属班级" style="width: 90%" @keyup.enter.native="getList">
                  <el-option
                    v-for="item in classOption"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="学院名称：">
                <el-input
                  @keyup.enter.native="getList"
                  placeholder="请输入学院名称"
                  style="width: 90%"
                  v-model="listQuery.academyName">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <div class="search--btn"  @click="getList">搜索</div>
            </el-col>
          </el-row>
          
        </el-form>
      </div>
      <div class="tableBody">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%">
          <el-table-column
            prop="studentCode"
            label="学号"
            >
          </el-table-column>
          <el-table-column
            prop="name"
            label="姓名">
          </el-table-column>
          <el-table-column
            label="入学年份">
            <template slot-scope="scope">
              {{ scope.row.grade + '级' }}
            </template>
          </el-table-column>
          <el-table-column
            prop="academyName"
            label="所属学院"
            >
          </el-table-column>
          <el-table-column
            prop="specialtyName"
            label="所属专业"
            >
          </el-table-column>
          <el-table-column
            prop="slaveClassName"
            label="所属班级">
          </el-table-column>
          <el-table-column
            prop="workUnit"
            label="工作单位">
          </el-table-column>
          <el-table-column
            prop="email"
            label="邮箱"
            >
          </el-table-column>
          <el-table-column label="操作" width="160">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.row)">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
              </el-button>
              <el-button style="color: #ff5858;" type="text" size="small" @click="handleDelete(scope.row)">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="listQuery.page"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="listQuery.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑弹框 -->
    <el-dialog :title="title" :visible.sync="dialogFormVisible" :close-on-click-modal='false' width="40%">
      <el-form :model="form" :rules="rules" ref="form"  label-width="120px">
        <el-form-item label="学号：" prop="studentCode">
          <el-input v-model="form.studentCode" auto-complete="off" placeholder="请输入学号" size="small"></el-input>
        </el-form-item>
        <el-form-item label="姓名：" prop="name">
          <el-input v-model="form.name" auto-complete="off" placeholder="请输入姓名" size="small"></el-input> 
        </el-form-item>
        <el-form-item label="所属学院：" prop="academyId">
          <el-select v-model="form.academyId" placeholder="请选择所属学院" size="small" style="width: 100%">
            <el-option
              v-for="item in academyOption1"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属专业：" prop="specialtyId">
          <el-select v-model="form.specialtyId" placeholder="请选择所属专业" size="small" style="width: 100%">
            <el-option
              v-for="item in specialtyOption1"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="入学年级：" prop="grade">
          <el-select v-model="form.grade" placeholder="请选择入学年级" size="small" style="width: 100%">
            <el-option
              v-for="item in options1"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分流班级：" prop="slaveClassCode">
          <el-select v-model="form.slaveClassCode" placeholder="请选择分流班级" size="small" style="width: 100%">
            <el-option
              v-for="item in classOption1"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="工作单位：" prop="workUnit">
          <el-input v-model="form.workUnit" auto-complete="off" placeholder="请输入工作单位" size="small"></el-input> 
          <!-- <el-select v-model="form.workUnit" placeholder="请选择工作单位" size="small" style="width: 100%">
            <el-option
              v-for="item in enterpriseOption"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select> -->
        </el-form-item>
        <el-form-item label="邮箱：" prop="email">
          <el-input v-model="form.email" auto-complete="off" placeholder="请输入邮箱" size="small"></el-input> 
        </el-form-item>
        <el-form-item label="手机号：" prop="phone">
          <el-input v-model="form.phone" auto-complete="off" placeholder="请输入手机号" size="small"></el-input> 
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('form')" v-show="title==='添加校友信息'" :loading="loading">确 定</el-button>
        <el-button type="primary" @click="editForm('form')" v-show="title!=='添加校友信息'" :loading="loading">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 导入弹框 -->
    <el-dialog title="导入数据" :visible.sync="dialogVisible" class="set-el-dialog set-course-dialog" width="35%" :close-on-click-modal="false">
      <div style="height:400px">
        <div style="height: 400px; min-width: 280px; width:50%; float: left">
          <el-steps direction="vertical" :active="active">
            <el-step title="上传文件" icon="el-icon-upload"></el-step>
            <el-step title="检查数据" :icon="icon" description="等待文件上传完毕后自动检查表格数据。"></el-step>
            <el-step title="完成" icon="el-icon-success"></el-step>
          </el-steps>
        </div>
        <div style="position: absolute;top: 105px;left: 85px;">
          <p style="padding: 10px 0;font-size:12px">导入数据前，请先<el-button type="text" size="mini" @click="download">下载模板</el-button>，按照模板格式录入数据。</p>
               <!-- :before-upload="beforeUpload" -->
          <div class="file-upload">
              <el-upload
               ref="uploadXls"
               :disabled="uploadDisabled"
               class="upload-demo"
               :action="uploadUrl"
               :on-success="handleSuccess"
               :on-progress="onProgress"
               :on-error="onError"
               :limit="1"
               :show-file-list="false"
               accept=".xls,.xlsx"
               name="file"
               :on-exceed="handleExceed">
              <el-button size="small" type="primary" @click="handleClick"><i class="el-icon-upload el-icon--right"></i>  上传文件</el-button>
              <div slot="tip" class="el-upload__tip">支持文件格式：.xls .xlsx。</div>
            </el-upload>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Crumb from '@/components/Crumb'
export default {
  components: { Crumb },
  data() {
    const validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback();
      } else {
        let reg = /^[1][3,4,5,7,8][0-9]{9}$/
        if(!reg.test(value)){
          callback(new Error('请输入正确的手机号码'))
        }else{
          callback()
        }
      }
    }
    return {
      academyOption: [],
      specialtyOption: [
        {
          id: '',
          name: '请先选择所属学院'
        }
      ],
      classOption: [
        {
          id: '',
          name: '请先选择所属专业'
        }
      ],
      academyOption1: [],
      specialtyOption1: [
        {
          id: '',
          name: '请先选择所属学院'
        }
      ],
      classOption1: [
        {
          id: '',
          name: '请先选择所属专业'
        }
      ],
      formLabelWidth: '120px',
      dialogFormVisible: false,
      icon: 'el-icon-refresh',
      dialogVisible: false,
      uploadDisabled: false,
      uploadUrl: '',
      active: 1,
      enterpriseOption: [],
      listQuery: {
        grade: null,
        academyId: '',
        specialtyId: '',
        slaveClassCode: '',
        academyName: '',
        page: 1,
        rows: 15
      },
      academyName:'',
      form: {
        studentCode: '',
        name: '',
        academyId: '',
        specialtyId: '',
        grade: '',
        slaveClassCode: '',
        workUnit: '',
        email: '',
        phone: ''
      },
      options: [],
      options1: [],
      tableData: [],
      total: 0,
      title: '添加校友信息',
      rules: {
        studentCode: [
          { required: true, message:'请输入学号', trigger: 'blur' }
        ],
        name: [
          { required: true, message:'请输入姓名', trigger: 'blur' }
        ],
        academyId: [
          { required: true, message:'请选择所属学院', trigger: 'change' }
        ],
        specialtyId: [
          { required: true, message:'请选择所属专业', trigger: 'change' }
        ],
        grade: [
          { required: true, message:'请选择入学年级', trigger: 'change' }
        ],
        slaveClassCode: [
          { required: true, message:'请选择分流班级', trigger: 'change' }
        ],
        email: [
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur' }
        ],
        phone: [
          { validator: validatePhone, trigger: 'blur' }
        ]
      },
      loading: false,
      bodyLoading: true,
      flag: true
    }
  },
  computed: {
    academyIdOfQuery() {
      return this.listQuery.academyId
    },
    specialtyIdOfQuery() {
      return this.listQuery.specialtyId
    },
    academyIdOfForm() {
      return this.form.academyId
    },
    specialtyIdOfForm() {
      return this.form.specialtyId
    }
  },
  watch: {
    academyIdOfQuery(val) {
      if(val) {
        this.getSpecialty(val, 1)
        this.listQuery.specialtyId = ''
        this.listQuery.slaveClassCode = ''
      }else {
        this.specialtyOption = [
          {
            id: '',
            name: '请先选择所属学院'
          }
        ]
        this.listQuery.specialtyId = ''
        this.classOption = [
          {
            id: '',
            name: '请先选择所属专业'
          }
        ]
        this.listQuery.slaveClassCode = ''
      }
    },
    specialtyIdOfQuery(val) {
      if(val) {
        this.getClass(val, 1)
        this.listQuery.slaveClassCode = ''
      }else {
        this.classOption = [
          {
            id: '',
            name: '请先选择所属专业'
          }
        ]
        this.listQuery.slaveClassCode = ''
      }
    },
    academyIdOfForm(val) {
      if(val) {
        this.getSpecialty(val, 0)
        if(!this.flag) {
          this.form.specialtyId = ''
          this.form.slaveClassCode = ''
        }
      }else {
        this.specialtyOption1 = [
          {
            id: '',
            name: '请先选择所属学院'
          }
        ]
        this.form.specialtyId = ''
        this.classOption1 = [
          {
            id: '',
            name: '请先选择所属专业'
          }
        ]
        this.form.slaveClassCode = ''
      }
    },
    specialtyIdOfForm(val) {
      if(val) {
        this.getClass(val, 0)
        if(!this.flag) {
          this.form.slaveClassCode = ''
        }
      }else {
        this.classOption1 = [
          {
            id: '',
            name: '请先选择所属专业'
          }
        ]
        this.form.slaveClassCode = ''
      }
    }
  },
  mounted() {
    this.getList()
    this.getGrade()
    this.getacademy()
  },
  methods: {
    resetListQuery() {
      this.listQuery = {
        grade: '',
        academyId: '',
        specialtyId: '',
        slaveClassCode: '',
        academyName: '',
        page: 1,
        rows: 15
      }
    },
    getList() {
      this.bodyLoading = true
      this.$post('/basic/alumniInfo/list', this.listQuery).then(res => {
        if(res.status === 1) {
          this.tableData = res.data
          this.total = res.total
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取列表失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    download() {
      location.href =  this.API_URL + '/basic/alumniInfo/template/alumniInfo'
    },
    getGrade() {
      this.$get('basic/class/grades').then(res => {
        if(res.status === 1) {
          // this.options = res.data
          this.options = [{
            id: '',
            name: '全部'
          }]
          res.data.forEach(item => {
            this.options.push(item)
          })
          this.options1 = res.data
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取所属学院失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    getacademy() {
      this.bodyLoading = true
      this.$get('/basic/academy/all?type=1').then(res => {
        if(res.status === 1) {
          this.academyOption = Object.assign([], res.data)
          this.academyOption.unshift({
            id: '',
            name: '全部'
          })
          this.academyOption1 = Object.assign([], res.data)

        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取所属学院失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    getSpecialty(academyID, type) {
      this.bodyLoading = true
      this.$get(`/basic/specialty/all/${academyID}`).then(res => {
        if(res.status === 1) {
          if(type) {
            this.specialtyOption = res.data
            this.specialtyOption.unshift({
              id: '',
              name: '全部'
            })
          } else {
            this.specialtyOption1 = res.data
            // this.specialtyOption1.unshift({
            //   id: '',
            //   name: '全部'
            // })
          }
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取所属班级失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    getClass(specialtyId, type) {
      this.bodyLoading = true
      this.$get(`/basic/class/shuntClassAll`, { specialtyId, type: 1 }).then(res => {
        if(res.status === 1) {
          if(type) {
            this.classOption = res.data
            this.classOption.unshift({
              id: '',
              name: '全部'
            })
          } else {
            this.classOption1 = res.data
            // this.classOption1.unshift({
            //   id: '',
            //   name: '全部'
            // })
          }
          
        } else {
          this.$notify({
            title: '提示',
            message: res.message,
            type: 'error',
            duration: '2000'
          })
        }
        this.bodyLoading=false
      }).catch(() => {
        this.$notify({
          title: '提示',
          message: '获取所属专业失败请重试!',
          type: 'error',
          duration: '2000'
        })
        this.bodyLoading=false
      })
    },
    // getEnterprise() {
    //   this.$post(`/basic/enterpriseInfo/menuList`).then(res => {
    //     if(res.status === 1) {
    //       this.enterpriseOption = res.data
    //       this.enterpriseOption.push({
    //         id: '',
    //         name: '无'
    //       })
    //     } else {
    //       this.$notify({
    //         title: '提示',
    //         message: res.message,
    //         type: 'error',
    //         duration: '2000'
    //       })
    //       this.enterpriseOption = [];
    //     }
    //   }).catch(() => {
    //     this.enterpriseOption = [];
    //     this.$notify({
    //       title: '提示',
    //       message: '获取工作单位失败请重试!',
    //       type: 'error',
    //       duration: '2000'
    //     })
    //   })
    // },
    handleAdd() {
      this.flag = false
      this.dialogFormVisible = true
      this.title = '添加校友信息'
      // this.getEnterprise()
      this.getGrade()
      this.$nextTick(() => {
        this.form = {
          studentCode: '',
          name: '',
          academyId: '',
          specialtyId: '',
          grade: '',
          slaveClassCode: '',
          workUnit: '',
          email: '',
          phone: ''
        }
        this.$refs['form'].resetFields();
      })
    },
    handleEdit(row) {
      this.flag = true
      this.dialogFormVisible = true
      this.title = '编辑校友信息'
      this.form = {
        id: row.id,
        studentCode: row.studentCode,
        name: row.name,
        academyId: row.academyId,
        specialtyId: row.specialtyId,
        grade: row.grade,
        slaveClassCode: row.slaveClassCode,
        workUnit: row.workUnit || '',
        email: row.email,
        phone: row.phone
      }
      // this.getEnterprise()
      this.getGrade()
      this.$nextTick(() => {
        this.$refs['form'].clearValidate()
        this.flag = false
      })
    },
    handleDelete(row) {
      this.$confirm('此操作将删除该校友信息, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$post(`/basic/alumniInfo/del/${row.id}`).then(res => {
          if(res.status === 1) {
            this.$notify({
              title: '提示',
              message: res.message,
              type: 'success',
              duration: '2000'
            })
            this.listQuery.page = 1
            this.getList()
          }else {
            this.$notify({
              title: '提示',
              message: res.message,
              type: 'error',
              duration: '2000'
            })
          }
        }).catch(() => {
          this.$notify({
              title: '提示',
              message: '删除失败！请重试！',
              type: 'error',
              duration: '2000'
            })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    handleSizeChange(val) {
      this.listQuery.rows = val
      this.getList()
    },
    handleCurrentChange(val) {
      this.listQuery.page = val
      this.getList()
    },
    submitForm(formName) {
      this.loading = true
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$post('/basic/alumniInfo/add', this.form).then(res => {
            if(res.status === 1) {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'success',
                duration: '2000'
              })
              this.dialogFormVisible = false
              this.resetListQuery()
              this.getList()
            }else {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'error',
                duration: '2000'
              })
            }
            this.loading = false
          }).catch(() => {
            this.loading = false
          })
        } else {
          this.loading = false
          return false
        }
      })
    },
    editForm(formName) {
      this.loading = true
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$post('/basic/alumniInfo/edit', this.form).then(res => {
            if(res.status === 1) {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'success',
                duration: '2000'
              })
              this.dialogFormVisible = false
              this.resetListQuery()
              this.getList()
            }else {
              this.$notify({
                title: '提示',
                message: res.message,
                type: 'error',
                duration: '2000'
              })
            }
            this.loading = false
          }).catch(() => {
            this.loading = false
          })
        } else {
          this.loading = false
          return false
        }
      })
    },
    handleClick() {
      this.uploadDisabled = false
    },
    handleExport() {
      this.active = 1
      this.dialogVisible = true
      let token = localStorage.getItem('x_token')
      this.uploadUrl = this.API_URL + '/basic/alumniInfo/importAlumniInfo' + `?x-token=${token}`
      this.$nextTick(() => {
        this.$refs.uploadXls.clearFiles()
      })
    },
    handleExceed(files, fileList) {
      this.$notify.warning({
        title: '提示',
        message: `当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`
      })
    },
    onProgress() {
      this.active = 2
      this.uploadDisabled = true
    },
    handleSuccess(res){
      if(res.status===1) {
        this.$notify.success({
          title: '提示',
          message: '导入成功!'
        })
        this.active = 3
        this.uploadDisabled = false
        this.getList()
      } else {
        if(res.data) {
          this.$notify.error({
            title: '提示',
            message: '导入失败!' + res.data[0].message
          })
        } else {
          this.$notify.error({
            title: '提示',
            message: res.message
          })
        }
        this.active = 1
        this.uploadDisabled = false
      }
      this.$nextTick(() => {
        this.$refs.uploadXls.clearFiles()
      })
    },
    onError() {
      this.$notify.error({
        title: '提示',
        message: '上传失败!请尝试重新上传!'
      })
      this.active = 1
      this.uploadDisabled = false
      this.$nextTick(() => {
        this.$refs.uploadXls.clearFiles()
      })
    }
  }
}
</script>
<style lang="scss">
.alumniInformation {
  padding-bottom: 50px;
  .mainContainer {
    overflow: hidden;
    margin-bottom: 50px;
    .title {
      line-height: 40px;
      margin-bottom: 20px;
      span {
        font-size: 14px;
        font-weight: 700;
        color: #666;
      }
      .buttonGroup {
        margin-bottom: 10px;
        overflow: hidden;
        line-height: 32px;
        float: right;
      }
    }
    .listQuery {
      background-color: #fff;
      padding: 20px 20px 0;
      border-radius: 10px;
      margin: 0 5px 20px;
      .el-form-item__label{
        font-size:14px;
        font-family:PingFang SC;
        font-weight:bold;
        color:rgba(105,105,105,1);
      }
    }
    .tableBody {
      overflow: initial;
      border-radius: 10px 10px 0 0;
      margin: 0 5px;
      .el-table {
        .distance {
          margin-right: 10px;
        }
        .el-table__header {
          tr {
            height: 50px;
            th {
              background: #fff;
              .cell {
                color: #666;
                font-weight: 700;
              }
            }
          }
        }
        .el-table__body {
          .el-table__row {
            height: 40px;
            td {
              padding: 0;
            }
          }
        }
      }
    }
    .pagination {
      padding: 20px;
      margin: 0 5px;
      text-align: right;
      background-color: #fff;
      border-radius: 0 0 10px 10px;
    }
  }
}
</style>



